.airspace-side-nav-bar { 
    background-color: rgb(247, 249, 250);
    height: 100vh !important;
    width: 100%;
}
.airspace-side-nav-bar-group {
    background-color: rgb(247, 249, 250);
}

.sideBarPortalSwitcher {
    position: absolute;
    /* bottom: 10px; */
    width: 100%;
}

.navBarNotificationMenu { 
    width: 350px;
    padding-left: 20px;
    padding-right: 20px;
    background-color: rgb(247, 249, 250);
    box-shadow: 0px 0px #888888;
    height: 500px;
    overflow: auto;
}

.navBarProfileMenu { 
    background-color: rgb(247, 249, 250);
    width: 180px;
}

.notificationIcon { 
    width: 30px;
    height: 30px;
}
.dynamic-delete-button {
  cursor: pointer;
  position: relative;
  top: 4px;
  font-size: 24px;
  color: #999;
  transition: all .3s;
}
.dynamic-delete-button:hover {
  color: #777;
}
.dynamic-delete-button[disabled] {
  cursor: not-allowed;
  opacity: 0.5;
}

